*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: normal;
}

ul {
  list-style-type: none;
}

img, video, embed, object {
  min-width: 100%;
}

img {
  vertical-align: middle;
}

label, button {
  cursor: pointer;
}

.clearfix::before, .clearfix::after, .grid::before, .grid::after {
  content: "";
  display: table;
}

.clearfix::after, .grid::after {
  clear: both;
}

html, body {
  background: #FFF;
  color: #000;
}

body, input, select, textarea, button {
  font-family: "Open Sans", sans-serif;
  font-size: 100%;
}

.grid {
  margin-left: -16px;
  margin-right: -16px;
}

.grid-xs--1, .grid-xs--2, .grid-xs--3, .grid-xs--4, .grid-xs--5, .grid-xs--6, .grid-xs--7, .grid-xs--8, .grid-xs--9, .grid-xs--10, .grid-xs--11, .grid-xs--12 {
  float: left;
  padding-left: 16px;
  padding-right: 16px;
}

